/* Container */
.footer{
display: grid;
grid-template-columns: repeat(4,1fr);
padding-top: 100px;
margin-top: 100px;
align-items: center;
grid-template-areas:
"logo location location social"

}

.social-media{
    text-align: center;
    width: 100%;
    grid-area: social;
}

.footer .logo{
    text-align: center;
    width: 100%;
    grid-area: logo;
}

/* facebook image */
.social-media img{
    width: 40px;
    height: 40px;
}
.logo img{
    width: 100px;
    height: 100px;
}
.footer .location{
grid-area: location;
text-align: center;
border-left: 2px black solid;
border-right: 2px black solid;
}